import { NgModule }             from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { DashboardComponent }   from './hero/dashboard.component';
import { HeroesComponent }      from './hero/heroes.component';
import { HeroDetailComponent }  from './hero/hero-detail.component';
import { HeroFormComponent }    from './account/hero-form.component';
// import { MyComponent }          from './hero/http-client.component';
import { LoginComponent }       from './account/hero-login.component';
import { LogoutComponent }      from './account/logout.component';
import { OnlyLoggedInUsersGuard }      from './services/accounts.service';
import { SignupGuard }      from './services/accounts.service';



const routes: Routes = [
  { 
  	path: '', 
    redirectTo: '/login', 
  	pathMatch: 'full',
  },

  { 
  	path: 'dashboard',  
  	component: DashboardComponent,
    canActivate: [OnlyLoggedInUsersGuard]
  }, 

  { 
  	path: 'detail/:id', 
  	component: HeroDetailComponent,
    canActivate: [OnlyLoggedInUsersGuard]
  }, 

  { 
  	path: 'heroes',    
  	component: HeroesComponent,
    canActivate: [OnlyLoggedInUsersGuard]   
  },

  {
    path: 'signup',
    component: HeroFormComponent,
    canActivate: [SignupGuard]
  },

  {
    path: 'login',
    component: LoginComponent,
    canActivate: [SignupGuard]
    
  },

  {
    path: 'logout',
    component: LogoutComponent,
    canActivate: [OnlyLoggedInUsersGuard]
    
  },
  
];

@NgModule({
  imports: [ RouterModule.forRoot(routes) ],
  exports: [ RouterModule ]
})
export class AppRoutingModule {}


