import { Injectable }    from '@angular/core';
import { Headers, Http } from '@angular/http';

import 'rxjs/add/operator/toPromise';
import { Observable }     from 'rxjs/Observable';
import 'rxjs/add/operator/map';

import { Hero } from '../hero/hero';
import { User } from '../hero/hero';
import { Router } from '@angular/router';
import {CanActivate} from "@angular/router";


@Injectable()
export class AccountsService {

  private headers:any = new Headers({'Content-Type': 'application/json'});
  private isLoggedIn : boolean = false;
  private non_field_error : boolean = false;
  private email_error : boolean = false;
  private username_error : boolean = false;

  constructor(private http: Http, private router: Router) { }

  getisLoggedIn():boolean{
    return this.isLoggedIn;
  }
  
  signup(data:User): Promise<any> {
    let user1:any;
    let data1:any;

    const url = `https://restheroes.herokuapp.com/api/rest-auth/registration/`;
    return this.http
      .post(url,JSON.stringify(data), {headers: this.headers})
      .toPromise()
      .then(response => {
         user1 = response.json()['key']
         this.username_error=false;
         this.email_error=false;
            if (user1) {
                localStorage.setItem('currentUser',user1);
            }
            data1=localStorage.getItem('currentUser');
            this.isLoggedIn = true;
            this.router.navigate(['dashboard'])
            console.log(data1)
          })
      .catch(response=>{
              
              user1 = response.json()
              if(user1.email){
              this.email_error=true;}

              if(user1.username){
              this.username_error=true;}
           
            });
  }


login(data:User): Promise<any> {
  let user1:any;
  let data1:any;
  const url = `https://restheroes.herokuapp.com/api/rest-auth/login/`;
          return this.http
          .post(url,JSON.stringify(data), {headers: this.headers})
          .toPromise()
          .then(response =>{
             user1 = response.json()['key']
                           this.non_field_error=false;
                if (user1) {
                    localStorage.setItem('currentUser', user1);
                }
                data1=localStorage.getItem('currentUser');
                this.isLoggedIn = true;
                this.router.navigate(['dashboard'])
                console.log(data1)
              })


            .catch(response=>{
              
              user1 = response.json()
              if(user1.non_field_errors){
              this.non_field_error=true;}
           
            });

    }

  logout1() {
        // remove user from local storage to log user out
        localStorage.removeItem('currentUser');
  }
  logout():Promise<any> {
    
        // remove user from local storage to log user out
        const url = `https://restheroes.herokuapp.com/api/rest-auth/logout/`;
                return this.http
                .post(url, {headers: this.headers})
                .toPromise()
                .then(response => {
                  
                  localStorage.removeItem('currentUser');
                  this.isLoggedIn = false;
                  this.router.navigate(['login'])
                })
                .catch(this.handleError);
              }
  private handleError(error: any): Promise<any> {
    console.error('An error occurred', error); // for demo purposes only
    return Promise.reject(error.message || error);
  }

  isLoggedInn(): boolean {
   let data:any;
    data=localStorage.getItem('currentUser');
    if(data)
       return true;
    else
       return false;
  }

}


@Injectable()
  export class OnlyLoggedInUsersGuard implements CanActivate {
   constructor(
     private AccountsService: AccountsService,
   ) {};
   canActivate() {
     console.log("OnlyLoggedInUsers");
     if (this.AccountsService.isLoggedInn()) {
       return true;
     } else {
       window.alert("You don't have permission to view this page");
       return false;
     }
   }
  }
  @Injectable()
  export class SignupGuard implements CanActivate {
   constructor(private AccountsService: AccountsService) {};
   canActivate() {
     if (this.AccountsService.isLoggedInn()) {
       window.alert("You are already logged in");
       return false;
     } else {
       return true;
     }
   }
  }


