import { NgModule }       from '@angular/core';
import { BrowserModule }  from '@angular/platform-browser';
import { FormsModule }    from '@angular/forms';
import { HttpModule }    from '@angular/http';

import { AppRoutingModule }     from './app-routing.module';


import { HttpClientModule } from '@angular/common/http';

import { AuthGuard } from './auth.guard';
import { AppComponent }        from './app.component';
import { HeroDetailComponent } from './hero/hero-detail.component';
import { HeroesComponent }     from './hero/heroes.component';
import { DashboardComponent }     from './hero/dashboard.component';
import { HeroService }         from './services/hero.service';
import { HeroSearchComponent }         from './hero/hero-search.component';
import { HeroFormComponent }         from './account/hero-form.component';
// import { MyComponent }          from './http-client.component'
import { LoginComponent }          from './account/hero-login.component'
import { LogoutComponent }          from './account/logout.component'
import { AccountsService } from './services/accounts.service';
import { OnlyLoggedInUsersGuard,SignupGuard }  from './services/accounts.service';



@NgModule({
  imports: [
    BrowserModule,
    FormsModule,
    AppRoutingModule,
    HttpModule,
    HttpClientModule,

  ],
  declarations: [
    AppComponent,
    DashboardComponent,
    HeroDetailComponent,
    HeroesComponent,
    HeroSearchComponent,
    HeroFormComponent,
    // MyComponent,
    LoginComponent,
    LogoutComponent,
  ],
  providers: [
    HeroService,
    // AuthService,
    AuthGuard,
    AccountsService,
    OnlyLoggedInUsersGuard,
    SignupGuard
  ],
  bootstrap: [ AppComponent ]
})
export class AppModule {
}


