import { Component } from '@angular/core';
import { AccountsService } from './services/accounts.service';

@Component({
	selector: 'my-app',
	styleUrls: ['./app.component.css'],
	template: `
  		<h1 style="padding-left:70px">{{title}}</h1>
  		<nav>
        <div *ngIf="this.AccountsService.isLoggedInn()" style="padding-left:70px">
    		  <a routerLink="/dashboard" routerLinkActive="active">Dashboard</a>
    		  <a routerLink="/heroes" routerLinkActive="active">Heroes</a>
          <a routerLink="/logout" routerLinkActive="active">Logout</a>
        </div>
        <div *ngIf="!this.AccountsService.isLoggedInn()" style="padding-left:70px">
          <a routerLink="/signup" routerLinkActive="active">Signup</a>
          <a routerLink="/login" routerLinkActive="active">Login</a>
        </div>
  		</nav>
  		<router-outlet></router-outlet>
	`,
})

export class AppComponent{
	title = 'Tour of Heroes';
  constructor(
    private AccountsService: AccountsService,
  ) {}

} 	



